        <p class="audio-time">{{ formatTime(currentTime[index]) }} / {{ formatTime(durations[index]) }}</p>
      </div>
      <div class="controls">
        <button @click="pauseAudio(index)">⏸</button>
        <input
          type="range"
          :max="durations[index]"
          :value="currentTime[index]"
          @input="seekAudio(index, $event)"
        />
      </div>
      <audio
        ref="audioElements"
        :src="audio.src"
        @timeupdate="updateTime(index)"
        @loadedmetadata="setDuration(index)"
