<script>
export default {
  data() {
    return {
      audios: [
        { name: "First programme", src: "audio.mp3" },
        { name: "Second programme", src: "audio.mp3" },
        { name: "Third programme", src: "audio.mp3" },
      ],
      currentTime: [0, 0, 0], 
      durations: [0, 0, 0], 
    };
  },
  methods: {
    playAudio(index) {
      this.$refs.audioElements[index].play();
    },
    pauseAudio(index) {
      this.$refs.audioElements[index].pause();
    },
    updateTime(index) {
      this.currentTime[index] = this.$refs.audioElements[index].currentTime;
    },
    setDuration(index) {
      this.durations[index] = this.$refs.audioElements[index].duration;
    },
    seekAudio(index, event) {
      const newTime = event.target.value;
      this.$refs.audioElements[index].currentTime = newTime;
      this.currentTime[index] = newTime;
    },
    formatTime(seconds) {
      const mins = Math.floor(seconds / 60);
      const secs = Math.floor(seconds % 60);
      return `${mins}:${secs < 10 ? "0" + secs : secs}`;
    },
  },
  name: "BaixaVisio",
};
</script>

